.settings {
  width: 100%;
  height: 7%;
  margin-bottom: 5%;
  color: white;
  border-top: 1px solid white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #162336;
  -webkit-box-shadow: 0 10px 6px -6px #777;
  -moz-box-shadow: 0 10px 6px -6px #777;
  box-shadow: 0 10px 6px -6px #777;
}

.settings__selection {
  border-radius: 25px;
  background: rgb(255, 255, 255, 0.5);
  width: 12%;
  padding: 0.4%;
  text-align: center;
  cursor: pointer;
  color: white;
}
.settings__selection:hover {
  background: rgb(210, 125, 13, 0.7);
  color: white;
}
.settings__selection-selected {
  background: rgb(210, 125, 13, 0.7);
  color: white;
}

@media only screen and (max-width: 1200px) {
  .settings__selection {
    width: 15%;
  }
}

@media only screen and (max-width: 950px) {
  .settings__selection {
    width: 100%;
  }
}
